html,body{font-family:"Nunito",sans-serif}#slider{display:flex;flex-direction:column;align-items:center}#raise-amount-slider{display:flex;flex-direction:row;align-content:stretch;position:relative;width:60%;height:2.5rem;user-select:none}#raise-amount-slider::before{content:" ";position:absolute;height:3px;width:100%;width:calc(100%*(4 / 5));top:50%;left:50%;transform:translate(-50%, -50%);background:#f2f2f2}#raise-amount-slider input,#raise-amount-slider label{box-sizing:border-box;flex:1;user-select:none;cursor:pointer}#raise-amount-slider label{display:inline-block;position:relative;width:20%;height:100%;user-select:none}#raise-amount-slider label::before{content:attr(data-raise-amount);position:absolute;left:50%;padding-top:0;margin-top:-0.75rem;transform:translate(-50%, 45px);font-size:14px;color:#f2f2f2;letter-spacing:.4px;font-weight:400;white-space:nowrap;opacity:.85;transition:all .15s ease-in-out}#raise-amount-slider label::after{content:" ";position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);width:20px;height:20px;border:2px solid #383838;background:#f2f2f2;border-radius:50%;pointer-events:none;user-select:none;z-index:1;cursor:pointer;transition:all .15s ease-in-out}#raise-amount-slider label:hover::after{transform:translate(-50%, -50%) scale(1.25)}#raise-amount-slider input{display:none}#raise-amount-slider input:checked+label::before{font-weight:800;opacity:1}#raise-amount-slider input:checked+label::after{border-width:4px;transform:translate(-50%, -50%) scale(0.75)}#raise-amount-slider input:checked~#raise-amount-pos{opacity:1}#raise-amount-slider input:checked:nth-child(1)~#raise-amount-pos{left:10%}#raise-amount-slider input:checked:nth-child(3)~#raise-amount-pos{left:30%}#raise-amount-slider input:checked:nth-child(5)~#raise-amount-pos{left:50%}#raise-amount-slider input:checked:nth-child(7)~#raise-amount-pos{left:70%}#raise-amount-slider input:checked:nth-child(9)~#raise-amount-pos{left:90%}#raise-amount-slider #raise-amount-pos{display:block;position:absolute;top:50%;width:12px;height:12px;background:#000;border-radius:50%;transition:all .15s ease-in-out;transform:translate(-50%, -50%);border:2px solid #fff;opacity:0;z-index:2}#raise-amount-slider:valid #raise-amount-slider input+label::before{transform:translate(-50%, 45px) scale(0.9);transition:all .15s linear}#raise-amount-slider:valid #raise-amount-slider input:checked+label::before{transform:translate(-50%, 45px) scale(1.1);transition:all .15s linear}#raise-amount-slider+button{display:block;position:relative;margin:56px auto 0;padding:10px 20px;appearance:none;transition:all .15s ease-in-out;font-family:inherit;font-size:24px;font-weight:600;background:#fff;border:2px solid #000;border-radius:8px;outline:0;user-select:none;cursor:pointer}#raise-amount-slider+button:hover{background:#000;color:#fff}#raise-amount-slider+button:hover:active{transform:scale(0.9)}#raise-amount-slider+button:focus{background:#4caf50;border-color:#4caf50;color:#fff;pointer-events:none}#raise-amount-slider+button:focus::before{animation:spin 1s linear infinite}#raise-amount-slider+button::before{display:inline-block;width:0;opacity:0;content:"";font-family:"Font Awesome 5 Pro";font-weight:900;margin-right:0;transform:rotate(0deg)}#raise-amount-slider:invalid+button{pointer-events:none;opacity:.25}@keyframes spin{from{transform:rotate(0deg);width:24px;opacity:1;margin-right:12px}to{transform:rotate(360deg);width:24px;opacity:1;margin-right:12px}}